let passwordInput = document.getElementById("password");
let confirmPasswordInput = document.getElementById("confirmPassword");
let registrationForm = document.getElementById("registrationForm");
//#region GLOBAL VARIABLES
let customFn = (args) => {
    // console.log(formObject.getInputElement("Password").value);
    return (formObject.getInputElement("ConfirmPassword").value == passwordInput.value);
};
let options = {
    rules: {
        Email: { required: true, email: true },
        Password: {
            required: true,
            minLength: [6, "Password should be atleast 6 characters"],
        },
        ConfirmPassword: {
            required: true,
            minLength: [customFn, "Password and Confirm password do not match!"],
        },
    },
};
let formObject = new FormValidator("#registrationForm", options);
//#endregion
//#region NOTIFICATION SETUP
let notification = new Message({
    content: "This is a sample notification",
    severity: "Success",
    showCloseIcon: true,
});
notification.appendTo("#notification");
notification.element.style.position = "absolute";
notification.element.style.top = "3.5%";
notification.element.style.right = "0";
notification.element.style.width = "40%";
notification.element.style.zIndex = "9999";
notification.element.style.display = "none";
notification.element.style.color = "White";
const notificationToggle = () => {
    if (notification.element.style.display == "none") {
        notification.element.style.display = "inline-flex";
        setTimeout(() => {
            notification.element.style.display = "none";
        }, 2000);
    }
    else {
        notification.element.style.display = "none";
    }
};
const showNotification = (message, severity, showCloseIcon) => {
    notification.content = message;
    notification.severity = severity;
    notification.showCloseIcon = showCloseIcon;
    notificationToggle();
};
//#endregion
confirmPasswordInput.addEventListener("keypress", (e) => {
    formObject.validate("ConfirmPassword");
});
passwordInput.addEventListener("keypress", (e) => {
    formObject.validate("Password");
});
console.log(formObject);
registrationForm.addEventListener("submit", (e) => {
    e.preventDefault();
    if (formObject.validate()) {
        $.ajax({
            url: "https://localhost:44345/Account/Register",
            method: "POST",
            data: {
                Email: formObject.getInputElement("Email").value,
                Password: formObject.getInputElement("Password").value,
                ConfirmPassword: formObject.getInputElement("ConfirmPassword").value,
            },
            success: function (response) {
                if (response.success == true) {
                    notification.element.style.color = "green";
                    showNotification("Registration Successfull!", "Success", true);
                    window.location.href = "/Account/Login";
                }
                else {
                    console.log(response);
                    let errorMessage = "";
                    response.errors.forEach((error) => {
                        errorMessage += error + "<br/>";
                    });
                    notification.element.style.color = "red";
                    showNotification(errorMessage, "Error", true);
                }
            },
            error: function (response) {
                let errorMessage = "";
                response.errors.forEach((error) => {
                    errorMessage += error + "<br/>";
                });
                showNotification(errorMessage, "Error", true);
            },
        });
    }
    else {
        console.log("Form is invalid");
    }
});
